.header-sticky {
    position: fixed!important;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 10;
    transition: all .5s ease-in;
    .header__inner {
        padding: 15px 0 15px 0;
    }
}

.header {
    position: relative;
    background-color: #f9f9f9;
    border-bottom: 2px solid #dedede;
    transition: all .5s ease-in;
    &__inner {
        display: flex;
        align-items: center;
        padding: 30px 0 28px 0;
        @media (max-width: 768px) {
            padding: 22px 0;
        }
        @media (max-width: 425px) {
            padding: 18px 0;
        }
    } 
    .logo {
        &__img {
            width: 160px;
            height: 45px;
            img {
                object-fit: contain;
            }
        }
    }
    &__search {
        margin-left: 15px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        position: relative;
        z-index: 100;
        svg {
            width: 17px;
            height: 18px;
            fill: $mainClr;
        }
    }
    &__input {
        display: flex;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
        width: 0;
        align-items: stretch;
        z-index: 11;
        opacity: 0;
        transition: all .5s ease-in;
        input {
            display: block;
            width: 100%;
            background-color: #f9f9f9;
            border: none;
            border-radius: 4px;
            height: 38px;
            font-size: 13px;
            color: #8a8a8a;
            font-weight: 500;
            padding: 0 15px;
            border-bottom: 1px solid $mainClr;
            border-radius: 0;
        }
        span {
            position: absolute;
            top: -25px;
            right: 15px;
            font-size: 16px;
            color: $mainClr;
            cursor: pointer;
        }
    } 
}
.header__input-toggle {
    max-width: 1200px;
    margin: 0 auto;
    width: 100%;
    padding-left: 300px;
    padding-right: 15px;
    opacity: 1;
    @media (max-width: 768px) {
        padding-left: 200px; 
     }
     @media (max-width: 510px) {
        padding: 0;
     }
    opacity: 1;
}
.menu {
    margin-left: auto;
    &__list {
        @include flex-b-c;
        @media (max-width: 976px) {
           position: absolute;
           left: 0;
           right: 0;
           top: 100%;
           flex-direction: column;
           justify-content: start;
           background-color: $mainClr;
           width: 100%;
           height: 100vh;
           z-index: 10;
           padding: 20px 15px;
           transform: translateX(-100%);
           opacity: 0;
           transition: all .3s ease-in;
        }
    }
    &__link {
        @include flex-i-c;
        font-size: 13px;
        color: #606060;
        font-weight: 800;
        text-transform: uppercase;
        height: 40px;
        padding: 0 30px;
        transition: all .3s ease-in;
        &:hover {
            background-color: $mainClr;
            border-radius: 4px;
            color: #ffffff;
        }
        @media (max-width: 1120px) {
            padding: 0 20px;
            height: 36px;
        }
    }
}


.menu__list-open {
    transform: translateX(0);
    opacity: 1;
    transition: all .8s ease-in-out;
}
.menu__icon-close {
     span {
        transition: all .3s ease-in-out;
        &:nth-child(1){
            transform: rotate(-45deg);
            top: 6px!important;
        }
        &:nth-child(2){
            opacity: 0;
            left: -50px;
        }
        &:nth-child(3){
            transform: rotate(45deg);
            bottom: 8px!important;
        }
    }
}

@media (max-width: 976px) {
    .menu__icon {
        width: 22px;
        height: 16px;
        margin-left: 15px;
        z-index: 10;
        transition: .5s ease-in-out;
        transform: rotate(0);
        cursor: pointer;
        span {
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            border-radius: 2px;
            background: $mainClr;
            display: block;
            opacity: 1;
            transition: .25s ease-in-out;
            transform: rotate(0);
            &:nth-child(1) {
                top:  0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
            }
            &:nth-child(3) {
                bottom: 0;
            }
        }
    }
}
