.main__wrapper {
    position: relative;
    background: url('../img/main-bg.png') no-repeat;
    width: 100%;
    height: 100%;
    background-position: top center;
}
.main {
    text-align: center;
    padding-top: 16.45%;
    padding-bottom: 12.3%;
    .main__subtitle {
        @include adaptFont(16, 24);
        line-height: 100%;
        color: #ffffff;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 25px;
        
        &:nth-child(1)._active{
            @include anim;
            animation-delay: 0s;
        }
        span {
            position: relative;
            display: inline-block;
            padding-bottom: 10px;
            &:after {
                position: absolute;
                content: '';
                left: 50%;
                transform: translateX(-50%);
                top: 100%;
                width: 90px;
                height: 3px;
                background-color: #ffffff;
            }

        }
        &-clr {
            color: #ac752f;
            &._active{
                @include anim;
                animation-delay: 1.5s;
            }

        }
        @media (max-width: 768px) {
           line-height: 140%;
        }
    }
    &__title {
        @include adaptFont(70, 185);
        line-height: 100%;
        text-transform: capitalize;
        color: #ffffff;
        font-weight: 400;
        font-family: "Droid Serif";
        margin-bottom: 40px;
        &._active{
            @include anim;
            animation-delay: .5s;
        }
    }
}


.about {
    &__inner {
        display: flex;
        background-color: #373737;
        filter: drop-shadow(0 0 40px rgba(43,43,43,0.3));
        margin: 50px  0;
        position: relative;
        @media (max-width: 620px) {
            flex-wrap: wrap;
         }
        p {
            margin-bottom: 40px;
            @media (max-width: 680px) {
               margin-bottom: 25px;
            }
            @media (max-width: 425px) {
               margin-bottom: 18px;
            }
        }
        .btn {
            margin-top: 10px;
        }
        
    }
    &__content {
        width: 50%;
        padding: 50px 70px;
        @media (max-width: 924px) {
           padding: 30px 40px;
        }
        @media (max-width: 768px) {
           width: 75%;
        }
        @media (max-width: 620px) {
            width: 100%;
         }
    }
    &__img {
        display: block;
        background: url('../img/person.png') no-repeat;
        background-size: cover;
        background-position: center right;
        width: 50%;
        margin: -50px 50px -50px 0;
        transform: scale(.7);
        transition-delay: .6s;
        transition: 1.2s;  
        will-change: transform;
        &._active {
            transform: scale(1);
        }
        @media (max-width: 914px) {
           margin: 0;
        }
        @media (max-width: 620px) {
            width: 100%;
         }
    }
}
.articles {
    &__slider {
        padding-top: 45px;
    }
    &__inner {
        text-align: center;
    }
    .swiper-slide:nth-child(even) {
        margin-top: 85px;
    }
}

.article {
    &__img {
        filter: drop-shadow(0 0 40px rgba(43,43,43,0.3));
        background-color: #373737;
        height: 236px;
        display: block;
        margin-bottom: 70px;
        margin-right: 30px;
        position: relative;
        img {
            @include pos-w;
            top: 30px;
            left: 30px;
            transform: scale(0.7);
        }
       &._active {
           img {
            transform: scale(1);
           }
       }
    }
    &__content {
        h5 {
            font-size: 20px;
            line-height: 28px;
            color: $mainClr;
            font-weight: 400;
            font-family: $droid;
            border-bottom: 1px solid $mainClr;
            padding-bottom: 15px;
        }
    }
    &__link {
        font-size: 16px;
        line-height: 28px;
        color: #ad7835;
        font-weight: 700;
        font-family: $rale;
        text-transform: uppercase;
        display: inline-block;
        position: relative;
        padding-right: 20px;
        transition: all .4s ease;
        &:after {
            position: absolute;
            content: '';
            top: 10px;
            transform: rotate(45deg);
            right: 0;
            width: 8px;
            height: 2px;
            background-color: $mainClr;
            border-radius: 1px;
            transition: all .4s ease;
        }
        &:before {
            position: absolute;
            content: '';
            top: 50%;
            transform:  rotate(-45deg);
            right: 0;
            width: 8px;
            height: 2px;
            background-color: $mainClr;
            border-radius: 1px;
            transition: all .4s ease;
        }
        &:hover {
            &:after,
            &:before {
                right: -10px;
            }
        }
    }
}

