body,
html {
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color: #7a7a7a;
}
*{
    box-sizing: border-box;
}
*:focus{
    outline: none;
}
a {
    text-decoration: none;
    cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6 + p {
    margin-top: 0;
}
p {
    margin: 15px 0;
}
img {
    max-width: 100%;
    width: 100%;
    object-fit: cover;
}
ul {
    margin: 0;
    padding: 0;
    li {
        list-style: none;
    }
}
svg {
    transition: .3s;
}
button {
    border: none;
}
button,
input,
select,
textarea {
    font-family: inherit;
}
$mainClr: #ad7835;
$greydark: #e4e4e4;
$dark-grey: #383838;

section {
    padding: 100px 0;
    @media (max-width: 768px) {
       padding: 70px 0;
    }
    @media (max-width: 425px) {
       padding: 50px 0;
    }
    @media (max-width: 320px) {
       padding: 30px 0;
    }
}
.container {
    max-width: 1330px;
    padding: 0 15px;
    margin: 0 auto;
}
.container-fluid {
    max-width: 100%;
    padding: 0;
}

.section__title {
    @include adaptFont(40, 60);
    line-height: 100%;
    color: #ad7835;
    font-weight: 400;
    font-family: $droid;
    margin-bottom: 40px;
    position: relative;
    &._active {
      @include anim;
      animation-delay: .5s;
    }
    span {
        display: inline-block;
        position: relative;
        padding-bottom: 15px;
        &:after {
            @include pos;
            top: 100%;
            width: 90px;
            height: 3px;
            background-color:$mainClr;
        }
    }
    @media (max-width: 700px) {
       margin-bottom: 30px;
    }
}


.btn {
    @include flex-i-c;
    height: 57px;
    background-color: #ffffff;
    border: 1px solid #6b4922;
    font-size: 16px;
    line-height: 28px;
    color: #ad7835;
    font-weight: 700;
    text-transform: uppercase;
    font-family: "Raleway", sans-serif;
    padding: 0 40px;
    background-color: transparent;
    white-space: nowrap;
}

p {
    @include adaptFont(13, 16);
    line-height: 28px;
    color: #ffffff;
    font-weight: 300;
}

    .btn {
      position: relative;
      @include flex-i-c;
      height: 57px;
      background-color: #ffffff;
      border: 1px solid #6b4922;
      @include adaptFont(13, 16);
      color: #ad7835;
      font-weight: 700;
      text-transform: uppercase;
      font-family: "Raleway", sans-serif;
      padding: 0 40px;
      background-color: transparent;
      cursor: pointer;
      white-space: nowrap;
      -webkit-font-smoothing: antialiased;
      @media (max-width: 768px) {
         height: 40px;
         padding: 0px 25px;
      }
      &:hover {
        border: none;
        .line-1 {
          animation: move1 1500ms infinite ease;
        }
        .line-2 {
          animation: move2 1500ms infinite ease;
        }
        .line-3 {
          animation: move3 1500ms infinite ease;
        }
        .line-4 {
          animation: move4 1500ms infinite ease;
        }
      }
    
    .line-1 {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      background-color:$mainClr;
      left: 0;
      bottom: 0;
    }
    .line-2 {
      content: "";
      display: block;
      position: absolute;
      height: 1px;
      background-color:$mainClr;
      left: 0;
      top: 0;
    }
    .line-3 {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      background-color:$mainClr;
      right: 0;
      top: 0;
    }
    .line-4 {
      content: "";
      display: block;
      position: absolute;
      height: 1px;
      background-color:$mainClr;
      right: 0;
      bottom: 0;
    }
  }
  
  @keyframes move1 {
    0% {
      height: 100%; 
      bottom: 0;
    }
    54% {
      height: 0; 
      bottom: 100%;
    }
    55% {
      height: 0; 
      bottom: 0;
    }
    100% {
      height: 100%; 
      bottom: 0;
    }
  }
  
  @keyframes move2 {
    0% {
      width: 0;
      left: 0;
    }
    50% {
      width: 100%; 
      left: 0;
    }
    100% {
      width: 0; 
      left: 100%;
    }
  }
  
  @keyframes move3 {
    0% {
      height: 100%; 
      top: 0;
    }
    54% {
      height: 0; 
      top: 100%;
    }
    55% {
      height: 0; 
      top: 0;
    }
    100% {
      height: 100%; 
      top: 0;
    }
  }
  
  @keyframes move4 {
    0% {
      width: 0; 
      right: 0;
    }
    55% {
      width: 100%; 
      right: 0;
    }
    100% {
      width: 0; 
      right: 100%;
    }
  }

  .anim-items img{
      transition-delay: .6s;
      transition: all .8s linear;
      transition: 1.2s;  
      will-change: transform;
  }

 