.footer {
    padding-bottom: 10.5%;
    &__social-list {
        padding-top: 7%;
        @include flex-c-c;
        margin-bottom: 35px;
        flex-wrap: wrap;
        li {
            @include flex-i-c;
            width: 80px;
            height: 80px;
            background-color: rgba(173,120,53,0);
            border: 1px solid #6b4922;
            margin: 0 12.5px;
            // &:hover {
            //     background-color: #373737;
            // }
            @media (max-width: 768px) {
               width: 60px;
               height: 60px;
            }
            @media (max-width: 520px) {
                width: 45px;
                height: 45px;
             }
             @media (max-width: 320px) {
                width: 32px;
                height: 32px;
                margin: 0 8px;
             }
        }
    }
    &__social-link {
        transition: all .3s ease-in;
        width: 35px;
        height: 40px;
        @media (max-width: 768px) {
           width: 28px;
           height: 30px;
        }
        @media (max-width: 520px) {
            width: 20px;
            height: 23px;
         }
         @media (max-width: 320px) {
            width: 15px;
            height: 18px;
         }
        svg {
            width: 100%;
            height: 100%;
            fill: $mainClr;
            &:hover {
                fill: #373737;
            }
        }
        
    }
}
