.gallery{
    text-align: center;
    &__inner {
        .section__title {
            margin-left: 70px;
            text-align: left;
            @media (max-width: 580px) {
               margin-left: 10px;
            }
        }
    }
    &__images {
        display: flex;
        flex-wrap: wrap;
        text-align: left;
        @media (max-width: 580px) {
           justify-content: center;
        }
    }
    &__left-indent {
        width: 34%; 
        overflow: hidden;
        @media (max-width: 580px) {
           width: 100%;
        }
    }
    &__left-img:nth-child(1) {
        padding: 70px 12.5px 12.5px 70px;
        @media (max-width: 768px) {
           padding: 10px;
        }
    }
    &__img {
        display: inline-flex;
        overflow: hidden;
        position: relative;
        filter: drop-shadow(0 0 40px rgba(43,43,43,0.3));
        padding: 12.5px;
        @media (max-width: 768px) {
           padding: 10px;
        }
            img {
                transform: scale(0.7);
            }
        &._active {
            img {
                transform: scale(1);
            }
        }
        &:hover {
            .gallery__img-content {
                left: 0;
            }
        }
    }
    &__right-indent {
        width: 66%;
        @media (max-width: 580px) {
           width: 100%;
        }
    }
    &__right-img:nth-child(1) {
        width: 45%;
    }
    &__right-img:nth-child(2) {
        width: 50%;
    }
    &__right-img:nth-child(4) {
        width: 45%;
        float: left;
    }
    &__img-content {
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .6);
        padding: 50px 20px 20px 50px;
        transition: all .5s linear;
        text-overflow: ellipsis;
        h5 {
            font-size: 20px;
            line-height: 28px;
            color: #ad7835;
            font-weight: 400;
            font-family: "DroidSerif";
            border-bottom: 1px solid #ad7835;
            padding-bottom: 15px;
        }
    }
   .btn {
       margin: 90px auto 0 auto;
   }
}


.contact {
    &__inner {
        display: flex;
        background-color: #373737;
        filter: drop-shadow(0 0 40px rgba(43,43,43,0.3));
        margin: 50px  0;
        @media (max-width: 1000px) {
           margin: 0;
        }
        @media (max-width: 620px) {
           flex-wrap: wrap;
        }
    }
    &__content {
        width: 50%;
        padding: 50px 70px;
        @media (max-width: 945px) {
           padding: 40px 50px;
        }
        @media (max-width: 840px) {
           width: 70%;
           padding: 30px 40px;
        }
        @media (max-width: 680px) {
           width: 100%;
        }
        @media (max-width: 425px) {
           padding: 20px;
        }
    }
    &__img {
        display: block;
        filter: drop-shadow(0 0 40px rgba(43,43,43,0.3));
        margin: -50px 0;
        margin-left: 65px;
        img {
            transform: scale(0.7);
            height: 100%;
        }
        &._active {
            img {
                transform: scale(1);
            }
        }
        @media (max-width: 945px) {
           margin: 0;
        }
        @media (max-width: 620px) {
           max-height: 450px;
        }

    }
    &__input,
    &__textarea {
        display: block;
        color: #ffffff;
        @include adaptFont(13, 16);
        line-height: 28px;
        font-family: $rale;
        font-weight: 300;
        border: none;
        border-bottom: 1px solid #737373;
        padding: 10px;
        margin-bottom: 30px;
        background-color: transparent;
        @media (max-width: 770px) {
           line-height: 23px;
           margin-bottom: 20px;
           padding: 5px;
        }
    }
    input::placeholder,
    textarea::placeholder {
        color: #ffffff;
        font-weight: 300;
    }
    input:focus,
    textarea:focus {
        border-color: $mainClr;
    }
    &__input {
        margin-right: 20px;
        float: left;
        width: 46%;
        @media (max-width: 1300px) {
           width: 100%;
        }
    }
    &__textarea {
        width: 100%;
        min-height: 115px;
        margin-bottom: 70px;
        @media (max-width: 768px) {
           margin-bottom: 50px;
        }
        @media (max-width: 425px) {
           margin-bottom: 30px;
        }
    }
}